    
var Lslide      = $('.left-item'),
    Rslide      = $('.right-item'),
    slideHeight = 500,
    index = 0;

function slideInDOM() {
    for (var x = 0; x < Lslide.length; x++) {
        Lslide[x] = $(Lslide[x]);        
    }
    for (var y = 0; y < Rslide.length; y++) {        
        Rslide[y] = $(Rslide[y]);        
    }
}

function moveToTop() {
    index++;
    slideInDOM();
    for (var el = 0; el < Lslide.length; el++) {
        var lTop = parseInt(Lslide[el].css("top")) + slideHeight + "px";
        var rTop = parseInt(Rslide[el].css("top")) - slideHeight + "px";
        Lslide[el].css("top", lTop);
        Rslide[el].css("top", rTop);
    }    

    if (index > Lslide.length-1) {
        index = 0;
        for (var el = 0; el < Lslide.length; el++) {
            var lTop = -slideHeight * el + "px";
            var rTop = slideHeight * el + "px";
            Lslide[el].css("top", lTop);
            Rslide[el].css("top", rTop);
        }
    }
}

function moveToBottom() {
    index--;
    slideInDOM();
    for (var el = 0; el < Lslide.length; el++) {
        var lTop = parseInt(Lslide[el].css("top")) - slideHeight + "px";
        var rTop = parseInt(Rslide[el].css("top")) + slideHeight + "px";
        Lslide[el].css("top", lTop);
        Rslide[el].css("top", rTop);    
    }

    if (index < 0) {
        index = Rslide.length-1;
        for (var el = 0; el < Lslide.length; el++) {
            var lTop = parseInt(Lslide[el].css("top")) + slideHeight * (Lslide.length - 1) + "px";
            var rTop = parseInt(Rslide[el].css("top")) - slideHeight * (Rslide.length - 1) + "px";
            Lslide[el].css("top", lTop);
            Rslide[el].css("top", rTop);
        }
    }
}
